/* 
    -- Table of Content --

    * # Preloader
    * # Margin
    * # InfoBox
    * # Breadcrumb
    * # Header
    * # About
    * # Project
    * # Project Details
    * # Credentials
    * # Service
    * # Blog
    * # Contact
    * # Footer
    * Responsive

*/
:root {
  --primary_color: #5b78f6;
  --dark: #323232;
}
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Inter", sans-serif;
  font-size: 14px;
  scroll-behavior: smooth;
  color: #9f9f9f;
  background: #0f0f0f;
  line-height: 19.2px;
}
img {
  max-width: 100%;
}
a {
  text-decoration: none;
}
.text-right {
  text-align: right;
}
.container {
  max-width: 1170px;
}

.gap-24 {
  gap: 24px;
}
.h-full {
  height: 100%;
}

/* # Preloader */
.preloader {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #111;
  transform-origin: bottom;
  z-index: 111111;
}

.preloader .black_wall {
  height: 100%;
  background-color: #222;
  transform-origin: top;
  animation: preloader_slide 0.5s ease-in-out 0s 1 normal both;
}

.preloader .loader {
  width: 100%;
  height: 3px;
  position: absolute;
  border-radius: 2px;
  top: 0;
  right: 0;
  left: 0;
  margin: auto;
  background-color: var(--primary_color);
  transform-origin: left;
  animation: loader_running 3s ease-in-out 1s infinite normal both;
}

.preloader.off {
  animation: preloader_slide 0.5s ease-in-out 0s 1 reverse both;
}

/* # Margin */
.mt-24 {
  margin-top: 24px;
}
.mb-24 {
  margin-bottom: 24px;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}
.flex-1 {
  flex: 1;
}
.theme-btn {
  background: var(--dark);
  color: #fff;
  display: inline-block;
  transition: 0.3s;
  border-radius: 16px;
  font-size: 16px;
  font-weight: 500;
  padding: 12px 30px;
}
.theme-btn:hover {
  background: #fff;
  color: var(--dark);
}
.shadow-box {
  position: relative;
  border-radius: 30px;
  background: #0f0f0f;
}
.shadow-box::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  /* background: linear-gradient(to right, rgb(255 255 255 / 15%), rgb(255 255 255 / 1%)); */
  background: linear-gradient(
    120deg,
    rgb(255 255 255 / 12%),
    rgb(255 255 255 / 1%)
  );
  content: "";
  z-index: -1;
  border-radius: 30px;
  margin: -1px;
}
.service-area
  .service-content-wrap
  .service-content-inner
  .service-item::before,
.shadow-box::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background: linear-gradient(
    120deg,
    rgba(255, 255, 255, 0.3),
    rgba(255, 255, 255, 0.05)
  );
  border-radius: 30px;
  opacity: 0.25;
}
.shadow-box * {
  z-index: 2;
  position: relative;
}
.section-heading {
  color: #ffffff;
  font-size: 74px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: -01px;
  display: flex;
  align-items: center;
  gap: 5px;
  margin-top: 2px;
  justify-content: center;
}
.section-heading img {
  display: block;
}

/* # InfoBox */
.info-box {
  padding: 24px;
}
.credential-area .credential-sidebar .overlay-link,
.contact-area .contact-form .overlay-link,
.project-details-wrap .project-about-2 .overlay-link,
.project-details-wrap .project-infos-wrap .overlay-link,
.projects-area .project-item .overlay-link,
.about-area .about-edc-exp .overlay-link,
.about-area .about-image-box .overlay-link,
.about-area .about-me-box .overlay-link,
.info-box .overlay-link {
  left: 0;
  top: 0;
  border-radius: 30px;
  width: 100%;
  height: 100%;
  opacity: 0;
  position: absolute;
  z-index: 9;
}
.credential-area .credential-sidebar .bg-img,
.contact-area .contact-form .bg-img,
.project-details-wrap .project-about-2 .bg-img,
.project-details-wrap .project-infos-wrap .bg-img,
.projects-area .project-item .bg-img,
.about-area .about-edc-exp .bg-img,
.about-area .about-image-box .bg-img,
.about-area .about-me-box .bg-img,
.info-box .bg-img {
  opacity: 0.1;
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 30px;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.info-box > img {
  display: block;
  margin: auto auto 16px auto;
}
.info-box .infos h4 {
  color: #bcbcbc;
  font-size: 12px;
  opacity: 0.5;
  margin-bottom: 6px;
  text-transform: uppercase;
  margin-top: 8px;
}
.info-box .infos h1 {
  font-size: 20px;
  color: #ffffff;
  font-weight: 500;
}
.info-box .about-btn {
  opacity: 0.2;
  transition: 0.3s;
  display: inline-block;
}
.info-box:hover .about-btn {
  opacity: 1;
}
.info-box .inner-profile-icons {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  padding: 22px;
  margin-bottom: 16px;
}
.info-box .inner-profile-icons.shadow-box:before {
  opacity: 0.1;
}
.info-box .inner-profile-icons.shadow-box:after {
  opacity: 0.5;
}
.info-box .inner-profile-icons a {
  width: 82px;
  height: 82px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  position: relative;
  box-shadow: 0 4px 24px -1px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: #fff;
  transition: 0.3s;
}
.info-box .inner-profile-icons a:hover {
  background: #ffffff;
  color: #0f0f0f;
}
.info-box .inner-profile-icons a:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  z-index: -1;
  opacity: 0.1;
  background: linear-gradient(
    120deg,
    rgba(255, 255, 255, 0.5),
    rgba(255, 255, 255, 0.1)
  );
  backdrop-filter: blur(40px);
}
.info-box .inner-profile-icons a i {
  font-size: 32px;
}
.info-box.about-services-box .icon-boxes {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 80px;
  margin-bottom: 56px;
  margin-top: 46px;
}
.info-box.about-services-box .icon-boxes i {
  font-size: 40px;
  color: #ffffff;
}
.info-box.about-client-box .clients .client-item {
  position: relative;
  border-radius: 30px;
  text-align: center;
  flex: 1;
  padding: 42px 30px 42px 30px;
}
.info-box.about-client-box .clients .client-item::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  opacity: 0.1;
  background: linear-gradient(
    120deg,
    rgba(255, 255, 255, 0.3),
    rgba(255, 255, 255, 0.05)
  );
  border-radius: 30px;
}
.info-box.about-client-box .clients .client-item h1 {
  color: #ffffff;
  font-size: 34px;
  margin-bottom: 17px;
  line-height: 1.2;
  letter-spacing: -1px;
}
.info-box.about-client-box .clients .client-item p {
  font-size: 12px;
  color: #bcbcbc;
  opacity: 0.5;
  margin: 0;
  text-transform: uppercase;
}
.info-box.about-contact-box {
  padding-bottom: 42px;
  padding-top: 83px;
}
.info-box.about-contact-box h1 {
  font-size: 44px;
  line-height: 52px;
  font-weight: 500;
  color: #fff;
  margin: 0;
}
.info-box.about-contact-box h1 span {
  color: var(--primary_color);
}
.info-box.about-contact-box .about-btn {
  position: absolute;
  display: block;
  right: 30px;
  bottom: 35px;
  opacity: 0.2;
  transition: 0.3s;
}
.info-box.about-contact-box:hover .about-btn {
  opacity: 1;
}
.info-box.about-contact-box .star-icon {
  position: absolute;
  left: 30px;
  top: 0;
  opacity: 0.5;
  margin: 0;
}
.info-box.about-profile-box,
.info-box.about-blog-box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* # Breadcrumb */
.breadcrumb-area {
  padding-top: 77px;
}
.breadcrumb-area .breadcrumb-content p {
  color: #bcbcbc;
  opacity: 0.7;
  margin-bottom: 24px;
}
.breadcrumb-area .breadcrumb-content .section-heading {
  font-size: 56px;
  position: relative;
  display: block;
  margin-bottom: 30px;
}
.breadcrumb-area .breadcrumb-content .section-heading img {
  display: initial;
}

/* # Header */
.header-area {
  margin-top: 0px;
  background-color: #0f0f0f;
}

.header-area .logo {
  display: inline-block;
  z-index: 16;
}
.header-area .logo img {
  display: block;
}
.header-area .navbar {
  margin: 0;
  padding: 0;
}
.header-area .navbar .theme-btn {
  display: none;
}
.header-area .navbar .menu {
  display: flex;
  align-items: center;
  gap: 49px;
  margin: 0;
  padding: 0;
}

.header-area .navbar .menu li a {
  font-size: 16px;
  color: #676767;
  transition: 0.3s;
  display: block;
  padding: 26px 0px;
}
.header-area .navbar .menu li a.active,
.header-area .navbar .menu li a:hover {
  color: #ffffff;
}

.header-area .show-menu {
  display: none;
  color: #fff;
  font-size: 25px;
  cursor: pointer;
  width: 25px;
  height: 18px;
  flex-direction: column;
  justify-content: space-between;
  z-index: 16;
}
.header-area .show-menu span {
  width: 100%;
  height: 1px;
  background: #fff;
  display: block;
  transition: 0.4s;
}
.header-area .show-menu.active span:first-child {
  transform: translate3d(0px, 6px, 0px) scale(1, 1) rotate(45deg) scale(1, 1);
}
.header-area .show-menu span:last-child {
  transform-origin: left;
}
.header-area .show-menu.active span:last-child {
  transform: translate3d(4px, -2px, 0px) scale(1, 1) rotate(-45deg) scale(1, 1);
}
.header-area .show-menu.active span:nth-child(2) {
  transform: scale(0);
}
.icon-name {
  opacity: 0; /* Hide the icon name by default */
  position: absolute;
  bottom: -25px; /* Adjust the positioning according to your design */
  left: 50%;
  transform: translateX(-50%);
  color: white;
  padding: 4px;
  border-radius: 4px;
  font-size: 12px;
  transition: opacity 0.3s ease-in-out;
}
a:hover .icon-name {
  opacity: 1;
}
.icon-name1 {
  opacity: 1; /* Hide the icon name by default */
  position: absolute;
  bottom: -25px; /* Adjust the positioning according to your design */
  left: 50%;
  transform: translateX(-50%);
  color: white;
  padding: 4px;
  border-radius: 4px;
  font-size: 8px;
  transition: opacity 0.3s ease-in-out;
  text-transform: none;
}

.icon-name1:hover {
  opacity: 0;
}

/* # About */
.about-area {
  padding-top: 150px;
}
.about-area .about-me-box {
  position: relative;
  padding: 50px 46px;
  display: flex;
  align-items: flex-start;
  gap: 30px;
}
.about-area .about-me-box .img-box {
  width: 224px;
  height: 224px;
  position: relative;
  overflow: hidden;
  background: linear-gradient(90deg, #3c58e3 -15%, #c2ebff 58%, #5ab5e2 97%);
  border-radius: 30px 0 30px 0;
  flex: none;
}
.about-area .about-me-box .img-box img {
  display: block;
  height: 100%;
  object-fit: cover;
  width: 100%;
}
.about-area .about-me-box .infos {
  padding-top: 30px;
  position: initial;
}
.about-area .about-me-box .infos h4 {
  color: #bcbcbc;
  opacity: 0.7;
  font-size: 14px;
  letter-spacing: 0.3px;
  margin-bottom: 3px;
}
.about-area .about-me-box .infos h1 {
  font-size: 36px;
  line-height: 40px;
  color: #fff;
  font-weight: 500;
  margin-bottom: 12px;
}
.about-area .about-me-box .infos p {
  letter-spacing: 0.3px;
  margin: 0;
}
.about-area .about-me-box .infos .about-btn {
  display: block;
  position: absolute;
  right: 30px;
  bottom: 30px;
  opacity: 0.2;
  transition: 0.3s;
}
.about-area .about-me-box:hover .infos .about-btn {
  opacity: 1;
}
.about-area .about-credentials-wrap .banner {
  padding: 17px 25px;
  margin-bottom: 24px;
}
.marquee {
  position: relative;
  width: 100%;
  max-width: 100%;
  height: 19px;
  overflow-x: hidden;
}

.marquee div {
  position: absolute;
  white-space: nowrap;
  will-change: transform;
  font-size: 12px;

  animation: marquee 8s linear infinite;
}

.about-area .about-credentials-wrap .banner .marquee b {
  color: #fff;
  font-weight: 400;
}
.about-area .about-credentials-wrap .banner .marquee img {
  display: inline-block;
  margin: 0 5px;
}
.about-area .about-credentials-wrap > .gx-row > * {
  flex: 1;
  width: 50%;
}

.about-area .about-image-box {
  width: 370px;
  flex: none;
  padding: 24px;
  border-radius: 30px;
}
.about-area .about-image-box .image-inner {
  background: linear-gradient(90deg, #3c58e3 -15%, #c2ebff 58%, #5ab5e2 97%);
  border-radius: 30px;
}
.about-area .about-image-box img {
  display: block;
  border-radius: 30px;
}
.about-area .about-details {
  flex: 1;
}
.about-area .about-details .about-details-inner {
  padding: 115px 60px 30px 30px;
  position: relative;
}
.about-area .about-details .about-details-inner img {
  position: absolute;
  left: 30px;
  top: 0;
}
.about-area .about-details .about-details-inner h1 {
  color: #ffffff;
  font-size: 36px;
  font-weight: 500;
}
.about-area .about-details .about-details-inner p {
  color: #ffffff;
  opacity: 0.8;
  line-height: 25px;
  margin: 0;
  font-size: 16px;
}
.about-area .about-edc-exp {
  padding: 30px;
}
.about-area .about-edc-exp h3 {
  font-size: 16px;
  text-transform: uppercase;
  color: #fff;
  margin-bottom: 23px;
}
.about-area .about-edc-exp ul li + li {
  margin-top: 32px;
}
.about-area .about-edc-exp ul li .date {
  color: #bcbcbc;
  opacity: 0.6;
  font-size: 16px;
  font-weight: 500;
  margin-bottom: 12px;
}
.about-area .about-edc-exp ul li h2 {
  font-size: 18px;
  color: #fff;
  opacity: 0.9;
  margin-bottom: 7px;
}
.about-area .about-edc-exp ul li .type {
  color: #bcbcbc;
  font-size: 14px;
  opacity: 0.6;
  font-weight: 400;
  margin: 0;
}

.main-aboutpage .about-area .about-contact-box {
  padding-top: 102px;
}
.stars1 {
  color: #9f9f9f;
}

/* # Project */
.projects-area {
