}
.projects-area .project-item {
  margin-bottom: 24px;
  padding: 16px 16px 26px 16px;
}
.projects-area .project-item .project-img {
  border-radius: 30px;
  margin-bottom: 13px;
}
.projects-area .project-item .project-img img {
